import React,{ useState, useEffect} from 'react';
import './Navbar.css'
import Category from '../CategoryDropDown/Category';
import Cart from '../Cart/Cart';
import { Link } from 'react-router-dom';
import { useDispatch,useSelector } from 'react-redux';
import { getProductByCategory } from '../../Redux/Actions/productActions';
import cart from '../../img/cart.png'
import heart from '../../img/heart.png'
import Form from '../Form/Form';

const Navbar = () => {
    const cartItems = useSelector((state) => state.cart.cartItems)
    let count = cartItems.length

    const [ showCart, setShowCart ] = useState(false);
    const handleClick = () => {
        setShowCart(!showCart);
    }

    const [ showForm , setShowForm ] = useState(false);
    const handleForm = () => {
        setShowForm(!showForm);
    }
    const closeForm = () => {
        setShowForm(false)
    }

    const [hamClick, setHamClick] = useState(false)
    const [ showMobileNav, setShowMobileNav ] = useState(false);
    const menuClick = () => {
        setHamClick(!hamClick)
        setShowMobileNav(!showMobileNav)
    }
    const handleMobileNav = () => {
        setShowMobileNav(!showMobileNav)
    }

    return(
        <>
        <div className="nav-wrapper">
            <div className="nav-container">
                <div className="logo-wrapper">
                    <div className="logo">lucid</div>
                </div>
                <div className="page-links-wrapper">
                    <Link to="/" className="link">
                        <div className="home">Home</div>
                    </Link>
                    <Link to="/products" className="link">
                        <div className="products">Products</div>
                    </Link>
                    <div className="reviews">Reviews</div>
                </div>
                <Category />
                <div className="nav-btn">
                    <div className="contacts-btn" onClick={ () => handleForm()}>Contacts
                    </div>
                    {showCart ? 
                        <div className="cart-container cart-container-show">
                            <Cart />
                        </div>
                        :
                        <div className="cart-container cart-container-hide">
                            <Cart />
                        </div>
                    }
                    <div className="carts-btn" onClick={() => handleClick()}><img src={cart} className="cart-icon"></img><div className="cartItemsNo">{count}</div></div>
                    <div className="wishlist-btn"><img src={heart} className="heart-icon"></img></div>
                    <div className={hamClick? "hamburger-menu-active" : "hamburger-menu"} onClick={() => menuClick()}></div>
                </div>
            </div>
        </div>
        <div className={showMobileNav ? "mobile-nav-wrapper" : "mobile-nav-wrapper-hide"}>
                    <Link to="/" className="link">
                        <div className="mobile-home" onClick={() => handleMobileNav()}>Home</div>
                    </Link>
                    <Link to="/products" className="link">
                        <div className="mobile-products">Products</div>
                    </Link>
                    <div className="mobile-reviews">Reviews</div>
                    <div className="mobile-contact-btn" onClick={ () => handleForm()}>Contacts
                    </div>
        </div>
        <div className={showForm ? "form-container" : "form-hide"}>
            <div className="form-close-btn" onClick={() => closeForm()}></div>
            <Form />
        </div>
        </>
    )
}
export default Navbar;