import { cartConstant } from "../Constants/cartConstants";

export const addToCart = ( product,productId,price,qty) => async(dispatch,getState) => {
    console.log(qty)
    dispatch({
        type: cartConstant.ADD_TO_CART,
        payload: { product,productId, price, qty},
    });
    localStorage.setItem("lucidStore",JSON.stringify(getState().cart.cartItems))
}

export const removeFromCart = (productId) => async(dispatch,getState) => {
    dispatch({
        type: cartConstant.REMOVE_FROM_CART,
        payload: { productId }
    })
    localStorage.setItem("lucidStore", JSON.stringify(getState().cart.cartItems))
}
export const cartEmpty = () => async(dispatch,getState) => {
    dispatch({
        type: cartConstant.CART_EMPTY
    })
    localStorage.setItem("lucidStore",JSON.stringify(getState().cart.cartItems))
}