import { cartConstant } from "../Constants/cartConstants";

export const cartReducers = (state = {cartItems : []}, {type,payload} ) => {

    switch(type){
        case cartConstant.ADD_TO_CART:
            const item = payload;
            const existedItem = state.cartItems.find(
                (el) => el.productId === item.productId
            )
            if(existedItem){
                return{
                    ...state,
                    cartItems: state.cartItems.map((x) => 
                        x.productId === item.productId ? item : x
                    )
                }
            }else{
                return{
                    ...state,
                    cartItems: [...state.cartItems, item]
                }
            }
        case cartConstant.REMOVE_FROM_CART:
            return{
                ...state,
                cartItems: state.cartItems.filter(
                    (x) => x.productId !== payload.productId
                )
            }
        case cartConstant.CART_EMPTY:
            return{
                ...state, cartItems : []
            }
        default: 
            return state;
    }
}