import React, { useEffect, useState } from 'react';
import './Cart.css'
import { useDispatch,useSelector } from 'react-redux';
import { addToCart, cartEmpty, removeFromCart } from '../../Redux/Actions/cartActions';
import { showModel } from '../../Redux/Actions/showModel';

const Cart = () => {
    const dispatch = useDispatch();
    const cartItems = useSelector((state) => state.cart.cartItems);

    const totalPrice = cartItems?.reduce((a, c) => {
        return Math.floor(a + c.price * c.qty)
    },0)

    const increaseQty = (item) => {
        dispatch(addToCart(item.product, item.productId, item.price, item.qty + 1))
    }
    const decreaseQty = (item) => {
        if( item.qty > 1){
            dispatch(addToCart(item.product, item.productId, item.price, item.qty - 1))
        }
    }

    const remove = (item) => {
        dispatch(removeFromCart(item.productId))
    }
    const empty = () => {
        if(cartItems.length > 0 ){
            dispatch(showModel())
            dispatch(cartEmpty())
        }

    }

    return(
        <>
        <div className="cart-wrapper">
            <div className="cart-title">
                Your Items
            </div>
        {cartItems?.map((item,index) => (
            <div className="cart-collection" key={index}>
                <div className="cart-img-wrapper"><img src={item.product.image} className="cart-img"></img></div>
                <div className="cart-item-detail">
                    <div className="cart-item-title">{item.product.title}</div>
                    <div className="cart-item-btn">
                        <div className="b i-btn" onClick={() => decreaseQty(item)}>-</div>
                        <div className="b qty">{item.qty}</div>
                        <div className="b d-btn" onClick={() => increaseQty(item)}>+</div>
                        <div className="b delect" onClick={() => remove(item)}>remove</div>
                    </div>
                    <div className="cart-item-price">{item.product.price}</div>
                </div>
            </div>
        ))}
        <div className="total-price-wrapper">
            <div className="total-price-title">
                Total Price  :
            </div>
            <div className="total-price">
                {totalPrice} $
            </div>
        </div>
        <div className="checkout-wrapper" onClick={() => empty()}>
            CheckOut
        </div>
        <div className="thanks-note">
            Thank you for Supporting us.
        </div>

        </div>
        </>
    )
}
export default Cart;