import React, { useRef, useState } from 'react';
import './Form.css'
import { Formik } from 'formik';
import emailjs from 'emailjs-com'
import { ToastContainer, toast } from 'react-toastify';
import "react-toastify/dist/ReactToastify.css";

const Form = () => {

    const form = useRef();

    const sendEmail = (values) => {


        // emailjs.sendForm( 'service_pnvz8z6' , 'template_fj56mzm' , form.current, 'user_ubipvwIhsyqQzy3VcDLcv')
        // .then((result) => {
        //     console.log(result.text);
        //     setTimeout(() => {
        //         setSent(true)
        //     },400)
        // },(error) => {
        //     console.log(error.text);
        // })
    }

    return(
        <>
        <Formik
            initialValues={{ name: '', message: '',email: '', password: ''}}
            validate={values => {
                const errors={};
                if( !values.email ) {
                    errors.email = 'Required';
                }else if(
                    !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(values.email)
                ) {
                    errors.email = 'Invalid email address';
                }
                return errors;
            }}
            onSubmit={( values, { setSubmitting ,resetForm}) => {
                console.log(values)
                setTimeout(() => {
                    sendEmail(values)
                    // alert('Thanks For Supporting');
                    setSubmitting(false);
                },400)
                resetForm();
                toast("thanks for supporting us")
                // toast({
                //     title: "Thanks for supporting us",
                //     status: "success",
                //     duration: 1000,
                //     isClosable: true,
                // })
            }}
            className="form-wrapper"
        >
            {({values,errors,handleSubmit,handleChange,isSubmitting}) => (
                <form ref={form} onSubmit={handleSubmit} className="form">
                    <legend className="form-title">Contact Us.</legend>
                    <input type="text" name="name" onChange={handleChange} value={values.name} className="username-input" placeholder="name"/>

                    <input type="email" name="email" onChange={handleChange} value={values.email} className="email-input" placeholder="e-mail"/>
                    {errors.email}
                    <input type="text" name="message" onChange={handleChange} value={values.message} className="message-input" placeholder="your message"/>

                    <button type="submit" disabled={isSubmitting} className="submit-btn">
                        Send
                    </button>
                    <ToastContainer />
                </form>

            )}
        </Formik>
        </>
    )
}
export default Form;