import { combineReducers } from "redux";
import { allProductsReducers , categoryProducts, singleProduct} from "./Reducers/productReducers";
import { cartReducers } from "./Reducers/cartReducers";
import { modelReducer } from "./Reducers/modelReducers";

export default combineReducers({
    allProducts: allProductsReducers,
    productsByCategory: categoryProducts,
    singleProduct: singleProduct,
    cart: cartReducers,
    showModel: modelReducer,
})