import { actionTypes } from "../Constants/productConstants";

export const allProductsReducers = (state = { products: []} , {type,payload} ) => {

    switch(type){
        case actionTypes.RELATED_REQUEST:
            return{ loading: true, products: [] };
        case actionTypes.RELATED_SUCCESS:
            return{ loading: false, products: payload};
        case actionTypes.RELATED_FAIL:
            return{ loading: false, error: payload};
        default:
            return state;
    }
}
export const categoryProducts = (state = { categoryProducts: []} , {type, payload} ) => {

    switch(type){
        case actionTypes.CATEGORY_REQUEST:
            return{ loading: true, categoryProducts: []};
        case actionTypes.CATEGORY_SUCCESS:
            return{ loading: false, categoryProducts: payload};
        case actionTypes.PRODUCTS_FAIL:
            return{ loading: true, categoryProducts: payload};
        default: 
            return state;
    }
}
export const singleProduct = (state = { selectedProduct: []}, {type, payload} ) => {

    switch(type){
        case actionTypes.SINGLE_REQUEST:
            return{ loading: true, selectedProduct: []}
        case actionTypes.SINGLE_SUCCESS:
            return{ loading: false, selectedProduct: payload}
        case actionTypes.SINGLE_FAIL:
            return{ loading: true, selectedProduct: payload}
        default: 
            return state;
    }
}