import React,{ useEffect, useState} from 'react';
import './Home.css'
import Footer from '../../Components/Footer/Footer';
import Navbar from '../../Components/Navbar/Navbar';
import { hideModel } from '../../Redux/Actions/showModel';
import { useSelector, useDispatch } from 'react-redux';
import men_img from '../../img/men.png'
import women_img from '../../img/women.png'
import jewelery_img from '../../img/jewelery.png'
import electronic_img from '../../img/electronic.png'
import { useHistory } from 'react-router';
import { getProductByCategory } from '../../Redux/Actions/productActions';
import Form from '../../Components/Form/Form';

const Home = () => {
    const showModel = useSelector((state) => state.showModel.showModel)
    const dispatch = useDispatch();
    const history = useHistory();
    const closeModel = () => {
        dispatch(hideModel())
    }


    const [ title, setTitle ] = useState("Men's clothing")
    const handleScroll = () => {

        const body = document.getElementById("home-body-wrapper")
        const body_pos = body.getBoundingClientRect().left - 20;
        const body_width = body.offsetWidth;
        const point1 = body_width /4;
        const point2 = body_width /2;
        const point3 = point1 + point2;
        const point4 = body_width;

        const slide2 = document.getElementById("slide2-title")
        const slide2_pos = slide2.getBoundingClientRect().left;

        if( body_pos < -point1 && body_pos > -point2){
            setTitle("Women's clothing")
        }else if( body_pos < -point2 && body_pos > -point3){
            setTitle("Jewelery")
        }else if( body_pos < -point3 && body_pos > -point4){
            setTitle("Electronics")
        }else{
            setTitle("Men's clothing")
        }
    }

    const [ posX, setPosX ] = useState(0)
    const [ posY, setPosY ] = useState(0)
    const [ cursorX, setCursorX ] = useState(0)
    const [ cursorY, setCursorY ] = useState(0)

    const mouseMove = (e) => {
        setPosX(e.pageX * 0.05)
        setPosY(e.pageY * 0.04)
        setCursorX(e.pageX)
        setCursorY(e.pageY)
    }


    
    useEffect(() => {

        const label = document.querySelectorAll('label')
        const texts = document.getElementById('text')
        const s2_t = document.getElementsByClassName('.s2-t')
        console.log(s2_t)
        console.log(label)
        label.forEach(text => {
            text.innerHTML = text.textContent.split('').map(( letter ) => 
            `<div class="letter-wrapper"><span class=letter>${letter}</span></div>`).join('')
        })

        const cursor = document.querySelector('.cursor-demo')
        console.log(cursor)
        cursor.innerHTML = cursor?.textContent.replace(/\S/g,'<span>$&</span>')
    
        const element = document.querySelectorAll('.cursor-demo span')
        for(let i =0; i< element.length ; i++){
            element[i].style.transform = "rotate("+i*20+"deg)";
        }
    }, [])

    const click = () => {

        const body = document.getElementById("home-body-wrapper")
        const body_pos = body.getBoundingClientRect().left - 20;
        const body_width = body.offsetWidth;
        const point1 = body_width /4;
        const point2 = body_width /2;
        const point3 = point1 + point2;
        const point4 = body_width;

        const slide2 = document.getElementById("slide2-title")
        const slide2_pos = slide2.getBoundingClientRect().left;

        if( body_pos < -point1 && body_pos > -point2){
            dispatch(getProductByCategory("women's clothing"))
            history.push('/products')
        }else if( body_pos < -point2 && body_pos > -point3){
            dispatch(getProductByCategory("jewelery"))
            history.push('/products')
        }else if( body_pos < -point3 && body_pos > -point4){
            dispatch(getProductByCategory("electronics"))
            history.push('/products')
        }else{
            dispatch(getProductByCategory("men's clothing"))
            history.push('/products')
        }
    }

    return(
        <>
        <div onMouseMove={mouseMove} className="home-container">
        <Navbar />
        <div className="home-header-wrapper">
                        <div className="slide-header-title">
                            {title}
                        </div>
                        <div className="slide-subhead">
                            <div className="sl-sh-fav">
                                Favourites
                            </div>
                            <div className="sl-sh-cmsn">
                                Coming soon
                            </div>
                        </div>
        </div>
        <div onScroll={handleScroll} onClick={click}  className="home-body-wrapper">

            <div className="home-body" id="home-body-wrapper">
                <div className="slide1" id="slide1">
                    <div className="slide1-title" id="slide-title">
                                <label id="s1-t1" className="text">Men</label>
                                <label id="s1-t2">'s</label>
                                <label id="s1-t3">Clothing</label>
                    </div>
                    <div className="slide-img-wrapper" style={{ transform: `translateX(-${posX}px) translateY(-${posY}px)`}}>
                        <img src={men_img} className="slide-img"></img>
                    </div>
                </div>
                <div className="slide2">
                    <div className="slide2-title" id="slide2-title">
                            <label id="s2-t1">Women</label>
                            <label id="s2-t2">'s</label>
                            <label id="s2-t3">Clothing</label>
                    </div>
                    <div className="slide-img-wrapper" style={{ transform: `translateX(-${posX}px) translateY(-${posY}px)`}}>
                        <img src={women_img} className="slide-img"></img>
                    </div>
                </div>
                <div className="slide3">
                    <div className="slide3-title" id="slide-title">
                                <label id="s3-t1">Jewe</label>
                                <label id="s3-t2">lery</label>
                    </div>
                    <div className="slide-img-wrapper" style={{ transform: `translateX(-${posX}px) translateY(-${posY}px)`}}>
                        <img src={jewelery_img} className="slide-img"></img>
                    </div>
                </div>
                <div className="slide4">
                    <div className="slide4-title" id="slide-title">
                                <label id="s4-t1">Elec</label>
                                <label id="s4-t2">tronic</label>
                    </div>
                    <div className="slide-img-wrapper" style={{ transform: `translateX(-${posX}px) translateY(-${posY}px)`}}>
                        <img src={electronic_img} className="slide-img"></img>
                    </div>
                </div>
            </div>
        </div>

            <div className="cursor-demo" style={{ top: `${cursorY}px`, left: `${cursorX}px`}}>
                Click to view product
            </div>

                {showModel ? <>
                        <div className="model-wrapper">
                            <div className="model">
                                <div className="model-title">
                                    Live up your lucid dream<br/>with lucid products.
                                </div>
                                <div className="model-subtitle">
                                    We appreciate your support.
                                </div>
                                <div className="close-model" onClick={() => closeModel()}>
                                    Back
                                </div>
                            </div>
                        </div>

                </> : ''}
                </div>
        </>
    )
}
export default Home;